import React, {Fragment} from 'react';
import TabsManager from "../../TabsManager/TabsManager";
import TransactionsTable from "../Transactions/TransactionsTable/TransactionsTable";
import {setNewEmailAuthModal, setChangePasswordAuthModal, setResetPassAuthModal, setSignInAuthModal, setSignUpAuthModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";

const Documentation = (props) => {
    return (
        <Fragment>
            <div className="dashboard-content">

            </div>
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setNewEmailAuthModal: (bool) => dispatch(setNewEmailAuthModal(bool)),
        setChangePasswordAuthModal: (bool) => dispatch(setChangePasswordAuthModal(bool)),
        setResetPassAuthModal: (bool) => dispatch(setResetPassAuthModal(bool)),
        setSignInAuthModal: (bool) => dispatch(setSignInAuthModal(bool)),
        setSignUpAuthModal: (bool) => dispatch(setSignUpAuthModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Documentation);