import React from "react";

import {Route} from "react-router-dom";

import DefaultComponent from "../defaultComponent/DefaultComponent";

import Licenses from "./../Licenses/Licenses";
import LicensesNew from "./../Licenses/LicensesNew/LicensesNew";
import Installer from "./Installer/Installer";
import Operations from "./Operations/Operations";
import Transactions from "./Transactions/Transactions";

import Profile from "./Profile/Profile";
import Tickets from "./Tickets/Tickets";
import Chat from "./Chat/Chat";
import NewTicket from "./NewTicket/NewTicket";

import Deals from "./Deals/Deals";
import Faq from "./Faq/Faq";
import NewDeal from "./NewDeal/NewDeal";
import ConfigShop from "../ConfigShop/ConfigShop";
import ConfigPurchased from "../ConfigShop/ConfigPurchased";
import DocumentationComponent from'./Documentation/Documentation';


import './style.scss';

const DashBoard = () => {

    const userId = 1234567890;
    const licensePath = '/licenses/' + userId;


    return (
        <div className='dashboard'>

            <Route path={licensePath+'/activity'} render={() => <Licenses activeTab='activity' />} />
            <Route path={licensePath+'/costs'} render={() => <Licenses activeTab='costs'/>} />
            <Route path={licensePath+'/users'} render={() => <Licenses activeTab='users'/>} />
            <Route path={'/licenses/new'} render={() => <LicensesNew />} />

            <Route path='/def' render={() => <DefaultComponent page='Default' />} />

            <Route path='/config-shop' render={() => <ConfigShop activeTab='config-shop'/>} />
            <Route path='/purchased' render={() => <ConfigPurchased activeTab='purchased'/>} />



            <Route path='/installer' render={() => <Installer />} />
            <Route path='/general' render={() => <Faq />} />

            <Route path='/tickets' exact render={() => <Tickets />} />
            <Route path='/tickets/:slug' render={() => <Chat />} />
            <Route path='/new-ticket' render={() => <NewTicket />} />


            <Route path='/deals' render={ () => <Deals /> } />
            <Route path='/new-deal' render={ () => <NewDeal /> } />

            <Route path='/profile' render={() => <Profile page='PROFILE' />} />

            <Route path='/operations' render={() => <Operations />} />
            <Route path='/transactions' render={() => <Transactions />} />

            <Route path='/documentation' render={() => <DocumentationComponent />} />

        </div>
    );
}

export default DashBoard;
